<template>
  <div id="header">
    <div id="avatar">
      <a href="https://oread0124.github.io/#/">
        <img src="../../assets/avatar.jpg" >
      </a>
    </div>
    <div id="contents">
      <barMenu id="menuBar" @message="changeArticleBody"></barMenu>
      <div class="article">
          <router-view/>
      </div>
      <p id="tail">
        © Oread | <a href="https://oread0124.github.io/#/" style="color: #ffffff;">黄振东</a>
      </p>
    </div>
  </div>
</template>

<script>

  import barMenu from '@/components/views/Bar'
  export default{
    name: "Header",
    components:{
      barMenu,
    },
    data (){
      return {
        articleBody: 'Home',
      }
    },
    methods:{
      changeArticleBody(val){
        this.$router.replace(val);
      },
    },
  }
</script>

<style>
  #contents {
    width: 90%;
    height: auto;
    margin: 1px auto;
  }
  #header {
    padding-top: 10px;
    height: 100%;
    background-color: #555d6f;
    text-align: center;
  }
  #avatar{
    background-color: #555d6f;
    height: auto;
    padding-bottom: 10px;
  }
  #avatar img {
    width: 70px;
    height: 70px;
    border: 3px solid #868686;
    box-sizing: border-box;
    border-radius: 35px;
  }
  #tail {
    margin-top: 5px;
    color: #acacac;
  }
  .article {
    width: 100%;
    min-height: 400px;
    margin: 0;
    padding: 0;
    border: none;
    background-color: #f0f0f0;
    border-radius:5px;
    margin-top: 5px;
  }
  #menuBar {
    position: sticky;
    position: -webkit-sticky;
    top: 0px;
  }
</style>



